import React, { useState, useEffect } from "react";
import {
  Card,
  CardBody,
  CardHeader,
  Col,
  Row,
  CardFooter,
  Button,
  Input,
  FormGroup,
  Label,
  Table,
} from "reactstrap";
import "react-toastify/dist/ReactToastify.css";
import { toast, ToastContainer } from "react-toastify";
import axios from "axios";
import Spinner from "reactstrap/lib/Spinner";

const SubProductSpecs = (props) => {
  const [mainCategoryFromServer, setMainCategoryFromServer] = useState([]);
  const [subCategoryFromServer, setSubCategoryFromServer] = useState([]);
  const [allProductSpecs, setAllProductSpecs] = useState([]);
  const [allSubProductSpecs, setAllSubProductSpecs] = useState([]);
  const [catID, setcatID] = useState("");
  const [productSpecsID, setProductSpecsID] = useState("");
  const [values, setValues] = useState({
    title: "",
    label: "",
  });
  const [submitLoading, setSubmitLoading] = useState(false);

  const handleState = (e) => {
    const { name, value } = e.target;
    setValues((prev) => ({ ...prev, [name]: value }));
  };

  useEffect(() => {
    //all category
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        query getAllCategory($page:Int,$limit:Int,$mainCategory:Boolean,$hasParent:Boolean,$catId:ID) {
          getAllCategory(input:{page:$page,limit:$limit,mainCategory:$mainCategory,hasParent:$hasParent,catId:$catId}){
            _id,
            name
          }
        }`,
        variables: {
          page: 1,
          limit: 30,
          mainCategory: true,
          hasParent: false,
          catId: null,
        },
      },
    })
      .then((res) => {
        const { getAllCategory } = res.data.data;
        setMainCategoryFromServer(getAllCategory);
      })
      .catch((err) => console.log(err));
  }, []);

  const handleSubmitMain = (e) => {
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        query getAllCategory($page:Int,$limit:Int,$mainCategory:Boolean,$hasParent:Boolean,$catId:ID) {
          getAllCategory(input:{page:$page,limit:$limit,mainCategory:$mainCategory,hasParent:$hasParent,catId:$catId}){
            _id,
            name
          }
        }`,
        variables: {
          page: 1,
          limit: 30,
          mainCategory: false,
          hasParent: true,
          catId: e.target.value,
        },
      },
    })
      .then((res) => {
        const { getAllCategory } = res.data.data;
        setSubCategoryFromServer(getAllCategory);
      })
      .catch((err) => console.log(err));
  };

  const handleProductSpecs = (e) => {
    setProductSpecsID(e.target.value);
  };

  const handleSubmit = (e) => {
    if (productSpecsID === "") {
      toast.warn("مقدار مشخصات نباید خالی باشد");
      return false;
    }
    if (values.title === "") {
      toast.warn("فیلد عنوان ریز مشخصات نباید خالی باشد");
      return false;
    }

    setSubmitLoading(true);
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        mutation addSubProductSpecs($name:String!,$label:String,$specs:ID!){
          subProductSpecs(input:{name:$name,label:$label,specs:$specs}){
            status,
            message
          }
        }
            `,
        variables: {
          name: values.title,
          label: values.label,
          specs: productSpecsID,
        },
      },
    })
      .then((res) => {
        setSubmitLoading(false);
        if (res.data.errors) {
          const { message } = res.data.errors[0].data[0];
          toast.error(message);
        } else {
          toast.success("ریز مشخصات برای این محصول ثبت شد");
          setValues({ title: "", label: "" });
        }
      })
      .catch((err) => console.log(err));
  };

  useEffect(() => {
    setAllProductSpecs([]);
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        query getAllProductSpecs($catId:ID!){
          getAllProductSpecs(catId:$catId){
            _id,
            name,  
            label
          }
        }
        `,
        variables: {
          catId: catID,
        },
      },
    })
      .then((res) => setAllProductSpecs(res.data.data.getAllProductSpecs))
      .catch((err) => console.log(err));
  }, [catID]);

  useEffect(() => {
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        query getAllSubProductsSpecs($specId:ID!){
          getAllSubProductSpecs(specId:$specId){
            _id,
            name,
            label
          }
        }
        `,
        variables: {
          specId: productSpecsID,
        },
      },
    })
      .then((res) => setAllSubProductSpecs(res.data.data.getAllSubProductSpecs))
      .catch((err) => console.log(err));
  }, [productSpecsID]);
  return (
    <div className="animated fadeIn">
      <ToastContainer />
      <Row>
        <Col xs="12">
          <Card>
            <CardHeader>اضافه کردن ریز مشخصات </CardHeader>
            <CardBody>
              <FormGroup row>
                <Col xs="2">
                  <FormGroup>
                    <Label htmlFor="Maincategory">دسته اصلی</Label>
                    <Input
                      type="select"
                      name="Maincategory"
                      id="Maincategory"
                      onChange={handleSubmitMain}
                    >
                      <option></option>
                      {mainCategoryFromServer.map((item, index) => (
                        <option key={item._id} value={item._id}>
                          {item.name}
                        </option>
                      ))}
                    </Input>
                  </FormGroup>
                </Col>
                <Col xs="2">
                  <FormGroup>
                    <Label htmlFor="subcategory">دسته دوم</Label>
                    <Input
                      type="select"
                      name="subcategory"
                      id="subcategory"
                      onChange={(e) => setcatID(e.target.value)}
                    >
                      <option></option>
                      {subCategoryFromServer.map((item, index) => (
                        <option key={item._id} value={item._id}>
                          {item.name}
                        </option>
                      ))}
                    </Input>
                  </FormGroup>
                </Col>
                <Col xs="2">
                  <FormGroup>
                    <Label htmlFor="subcategory">مشخصات محصول</Label>
                    <Input
                      type="select"
                      name="subcategory"
                      id="subcategory"
                      onChange={handleProductSpecs}
                    >
                      <option></option>
                      {allProductSpecs.map((item, index) => (
                        <option key={item._id} value={item._id}>
                          {item.name}
                        </option>
                      ))}
                    </Input>
                  </FormGroup>
                </Col>
                <Col xs="3">
                  <FormGroup>
                    <Label htmlFor="title">عنوان ریز مشخصات</Label>
                    <Input
                      type="text"
                      placeholder="عنوان ریز مشخصات را وارد نمایید"
                      name="title"
                      value={values.title}
                      onChange={(e) => handleState(e)}
                      required
                    />
                  </FormGroup>
                </Col>
                <Col xs="3">
                  <FormGroup>
                    <Label htmlFor="label">توضیح ریز مشخصات</Label>
                    <Input
                      type="text"
                      placeholder="توضیحات ریز مشخصات را وارد نمایید"
                      name="label"
                      value={values.label}
                      onChange={(e) => handleState(e)}
                      required
                    />
                  </FormGroup>
                </Col>{" "}
              </FormGroup>
            </CardBody>
            <CardFooter>
              <Button
                type="submit"
                color="primary"
                size="sm"
                onClick={handleSubmit}
              >
                {submitLoading ? <Spinner size="sm" /> : "ثبت ریز مشخصات"}
              </Button>
            </CardFooter>
          </Card>
        </Col>
      </Row>

      <Row>
        <Col>
          <Card>
            <CardHeader>
              <Row>
                <Col xs="4">
                  <i className="fa fa-align-justify"></i>
                  <span style={{ marginRight: "5px" }}>
                    لیست همه ریز مشخصات محصول
                  </span>
                </Col>
              </Row>
            </CardHeader>
            <CardBody>
              <Table striped responsive size="sm">
                <thead>
                  <tr>
                    <th>عنوان ریز مشخصات</th>
                    <th>توضیحات ریز مشخصات</th>
                  </tr>
                </thead>
                <tbody>
                  {allSubProductSpecs.map((item, index) => (
                    <tr key={item._id}>
                      {" "}
                      <td>{item.name}</td>
                      <td>{item.label}</td>
                    </tr>
                  ))}
                </tbody>
              </Table>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default SubProductSpecs;
